
import { onMounted, ref, watch } from "vue";
import ApiService from "../../core/services/ApiService";
import { useI18n } from "vue-i18n";
import { useField } from "vee-validate";

export default {
  name: "EquipmentTypesSelect",
  props: {
    equipment_type_id: {
      type: String,
      default: "",
    },
    showLabel: {
      type: Boolean,
      default: false,
    },
    size: {
      type: String,
      default: "small",
    },
    required: {
      type: Boolean,
      default: true,
    },
  },
  emits: ["update:equipment_type_id"],

  setup(props) {
    const { t } = useI18n();
    const inputEquipmentType = ref({
      loading: false,
      options: [],
      list: [],
    });

    const isEquipmentTypeRequired = (value) => {
      if (props.required && !value) {
        return t("requipmenttype");
      }
      return true;
    };

    const { errorMessage, value: element_id } = useField(
      "equipmentType",
      isEquipmentTypeRequired
    );

    const getEquipmentType = () => {
      ApiService.get("/api/equipment-types").then(({ data }) => {
        inputEquipmentType.value.list = data;
        inputEquipmentType.value.options = data;
      });
    };
    const selectEquipmentType = (query) => {
      if (query !== "") {
        inputEquipmentType.value.loading = true;
        setTimeout(() => {
          inputEquipmentType.value.loading = false;
          inputEquipmentType.value.options =
            inputEquipmentType.value.list.filter(
              (item: Record<string, any>) => {
                return (
                  item.name.toLowerCase().indexOf(query.toLowerCase()) > -1
                );
              }
            );
        }, 200);
      } else {
        inputEquipmentType.value.options = [];
      }
    };

    watch(
      () => props.equipment_type_id,
      (first) => {
        if (first) {
          element_id.value = first;
        }
      }
    );

    onMounted(() => getEquipmentType());

    return {
      element_id,
      inputEquipmentType,
      errorMessage,
      getEquipmentType,
      selectEquipmentType,
    };
  },
};
